* {
    box-sizing: border-box;
}

html,
body {
    margin: 0;
    padding: 0;
    font-size: 10px;
}

body {
    height: 100vh;
    width: 100vw;
}

.visualiser {
    height: 80vw;
    width: 80vw;
    margin: 20px auto;
    display: flex;
    flex-flow: row wrap;
}

.node {
    background-color: blue;
    border: 1px solid black;
    padding: 0;
    margin: 0;
}

.weighted-node {
    background-image: url('anchor.png');
    background-repeat: no-repeat;
    background-position: center;
    background-size: 70%;
}

.wall-node {
    background-color: black;
    border-color: lightseagreen;
}
